import React from "react"

class CMysql extends React.Component{
    render(){
        return(
            <div className="row justify-content-lg-end">
                <div className="col-12 col-lg-8 col-xl-9 p-5 parrafo_post">
	    	  	   <p class="p-2">Te has preguntado alguna vez donde se almacena la informacin de los usuarios ya sea que ests utilizando una aplicacin o en un sitio de internet, esto es gracias a las <strong>bases de datos</strong> donde se guardan grandes cantidades de informacin.</p>
	    	  	   <p class="p-1"> Un ejemplo seria <strong>Facebook</strong> ellos almacena mucha informacin de cada usuario como nombres y apellidos completos, edad, fecha de nacimiento, nombre Usuario, contrasea, foto de perfil, direccin, numero telefnico y mucho ms.</p>
	    	  	   <p class="p-1"> Pero tambin existe el mtodo <strong>entidad relacion</strong> que es uno de los ms utilizados y una vez ms Facebook lo utiliza es por eso que en tu perfil existen la seccin amigos en comn por qu tu cuenta puede estar relacionado con otras, es por eso que solo tus amigos puede de enterarse de tus ltimas publicaciones. Los <strong>Like</strong> tambin se relacionan ya que puedes dar like no solo a una publicacin sino a muchas y de diferentes personas de igual manera sucede con los comentarios.</p>
	    	  	   <p class="p-1">Ahora hablemos sobre aquellos <strong>gestores de bases de datos</strong> la verdad es que existen muchos, algunos tienen mucho en comn y otros tienen diferencias estos son:</p>
	    	  	   <p class="p-1"><strong>MySQL</strong>  <strong>postgreSQL</strong>  <strong>SqlLite</strong>  <strong>SQLserver</strong> - <strong>Mongo DB</strong></p>
	    	  	   <p class="p-1">Uno de los ms populares es MySQL, es muy utilizado para aplicaciones web como de escritorio y contiene entidad relacin lo cual es muy bueno. Si deseas aprender esta tecnologa te invito a descargarlo de internet y empieza a aprender sin lmites. Una vez instalado en tu ordenador puedes comenzar a crear una base de datos completamente nueva, lo puedes hacer con la interfaz grfica o mediante la <strong>lnea de comandos</strong>.</p>
	    	  	   <p class="p-1">Una base de datos contiene tablas esas tablas contiene  filas y columnas estas filas y columnas contiene campos y atributos, si gustas puedes profundizar este tema por ti mismo.</p>
	    	  	   <p class="p-1">Imaginemos que tienes un <b>mini Marquet</b> y necesitas registrar los productos que tiene en el almacn, entonces creas una tabla llamada <b>Productos-Almacenados</b> con sus respectivos campos como: <code>ID-prod</code>, <code>Nombre-prod</code>, <code>Cantidad-prod</code>, <code>fecha-de-caducacion</code> y con sus respectivos atributos como: tipo de dato varchar, int, float, text, number y la longitud.</p>
	    	  	   <p class="p-1">Una vez termina tu estructura empiezas a llenar tu tabla de informacin pero <b>Cmo lo hacemos?</b> Existen comandos para realizar estas tares como: <code>"INSERT INTO SET"</code> que nos permite insertar informacin veamos todo el procedimiento.</p>
	    	  	   <p class="p-1"><code>INSERT INTO</code> Producto-Almacenado <code>SET</code> <b>Nombre-prod</b>=leche, <b>Cantidad-prod</b>=32, <b>Fecha-de-caducacion</b>=02-03-2020;</p>
	    	  	   <p class="p-1">Ahora necesitas mostrar la informacin y eso lo hacemos con el comando <code>SELECT</code> veamos el proceso completo.</p>
	    	  	   <p class="p-1"><code>SELECT * FROM</code> Producto-Almacenado;</p>
	    	  	   <p class="p-1">Este nos permite abstraer o mostrar toda la informacin que est en nuestra tabla, pero ahora imaginemos que solo necesitas mostrar un registro en especfico, para eso utilizamos <code>WHERE</code> veamos el ejemplo.</p>
	    	  	   <p class="p-1"><code>SELECT * FROM</code> Producto-Almacenado <code>WHERE</code> <b>Nombre-prod</b>=leche;</p>
	    	  	   <p class="p-1">Ahora solo se mostrar aquel registro que tenga en su campo Nombre-prod el valor de Leche;</p>
	    	  	   <p class="p-1">Ahora imagina que tus clientes comprar leche diariamente por lo tanto tu registro debe ser actualizado y <b>Cmo hacemos eso?</b> Veamos el comando a continuacin.</p>
	    	  	   <p class="p-1"><code>UPDATE</code> Producto-Almacenado <code>SET</code> <b>Cantidad-prod</b>=25 <code>WHERE</code> <b>Nombre-prod</b>=leche;</p>
	    	  	   <p class="p-1">El comando UPDATE sirve para actualizar o modificar un registro aunque debes de escribir el nombre de la tabla y el campo que vas a cambiar, si te distes cuenta he utilizado <code>WHERE</code> porque necesito especificar aquel registro a modificar, si en algn caso no utilizara WHERE el resultado sera que todos los campos se modificara y en realidad no quisieras eso.</p>
	    	  	   <p class="p-1">Ahora imagina que descartaras un producto porque ya no lo vendes en tu mini Marquet, necesitas eliminar de tu base de datos y <b>Cmo lo hacemos?</b> Veamos el comando.</p>
	    	  	   <p class="p-1"><code>DELETE FROM</code> Producto-Almacenado <code>WHERE</code> <b>Nombre-prod</b>=Leche;</p>

	    	  	   <div class="row">
	   	   	   	        <div class="col-12 col-md-6">
	   	   	   		        <p class="p-1">El comando <code>DELETE</code> sirve para eliminar registro pero necesitamos especificar la tabla y el registro, y tambin he utilizado <code>WHERE</code> para especificar el producto que ya no vender o el producto que deseo eliminar, si no lo utilizara probablemente se eliminar todos tus registros de tu tabla.</p>
	   	   	   	        </div>

	   	   	   	        <div class="col-12 col-md-6">
	   	   	   		        <div id="sabias">
	   	   	   			        <h6 class="text-center">Sabias esto?</h6>
	   	   	   			        <p class="p-2">Las tablas contiene claves Primarias o PRIMARY KEY  es un campo o a una combinacin de campos que identifica de forma nica a cada fila de una tabla. 
						        Claves forneas  es una limitacin referencial entre dos tablas. Entender esto es muy importante si utilizaras entidad relacin.
						        </p>
	   	   	   		        </div>
	   	   	   	        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default CMysql;